import BgList from '../components/BgSetting/BgList'
export default {
  name: 'BgSetting',
  components: {
    BgList
  },
  beforeRouteEnter (to, from, next) {
    document.title = '牛霸霸屏商户管理-背景设置'
    next()
  },
  data () {
    return {
      imageSelected: 1,
      images: [{
        id: 1
      }, {
        id: 2
      }, {
        id: 3
      }],
      activeName: 'VideoBg',
      videoSelected: 2,
      videos: [{
        id: 1
      }, {
        id: 2
      }, {
        id: 3
      }]
    }
  },
  mounted () {

  }
}



